
#home {
    height: 100%;
    width: 100%;
}

.stage-header{
    position: fixed;
    height: .88rem;
    width: 100%;
    background: #d43d3d;
    z-index: 1;
}
.header-left{
    position: relative;
    height: .5rem;
    margin-left: .48rem;
    display: inline-block;
}
.header-message{
    position: relative;
    top: -.36rem;
    width: .5rem;
    height: .5rem;
}
.header-center{
    display: inline-block;
    width: 4.5rem;
    height: .88rem;
    text-align: center;
}
.header-logo{
    position: relative;
    top: -.4rem;
    width: 1.7rem;
    height: .4rem;
    display: inline-block;
}
.header-refresh{
    position: relative;
    top: -.44rem;
    width: .3rem;
    height: .3rem;
    display: inline-block;
    margin-left: .1rem;
}
.rotate {
  -webkit-transform-style: preserve-3d;
  -webkit-animation: x-spin 0.7s linear;
}
@-webkit-keyframes x-spin {
0% {
    -webkit-transform: rotateZ(0deg);
}
50% {
    -webkit-transform: rotateZ(180deg);
}
100% {
    -webkit-transform: rotateZ(360deg);
}
}

.header-silder{
    position: fixed;
    top: .88rem;
    width: 100%;
    background: #f4f5f6;
    border-bottom: .01rem solid #ddd; 
    z-index: 1;
}
.silder-list{
    width: 6.67rem;
    height: .72rem;
    padding: .1rem .1rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    overflow-x: scroll;
    list-style: none;
    display: -webkit-box;
}
.silder-list li{
    width: .68rem;
    height: .52rem;
    font-size: .34rem;
    padding: 0rem .24rem;
    color: #505050bf;
}
.current{
    color: #f85959!important;
}
.add-chanel{
    position: absolute;
    width: .8rem;
    right: 0px;
    height: .72rem;
    /* background: #f85959; */
    top: -.01rem;
}
.add-btn{
    position: absolute;
    top: .14rem;
    left: .2rem;
    width: .4rem;
    height: .4rem;
}

.message{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1000;
    background: rgba(0, 0, 0, 0.5);
}
.message-box{
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -2.7rem;
    margin-top: -2.25rem;
    width: 5.4rem;
    height: 4.5rem;
    background: #ffffff;
    border-radius: .1rem;
}
.message-close{
    background: url(data:image/png;base64,) no-repeat center;
    background-size: .34rem;
    position: absolute;
    right: 0px;
    top: 0px;
    width: .8rem;
    height: .68rem;
}
.message-banner{
    position: relative;
    top: .2rem;
    height: 2.42rem;
    width: 3.3rem;
    display: block;
    margin: 0 auto;
}
.message-tip{
    margin-top: .26rem;
    margin-bottom: .48rem;
    font-size: .34rem;
    text-align: center;
    color: #a5a5a5;
}
.download-btn{
    font-size: .4rem;
    text-align: center;
    background: #ff6762;
    color: #fff;
    border-radius: .1rem;
    text-shadow: 0px 1px 1px #f0221c;
    padding: .1rem .4rem;
    margin: 0rem .5rem;
}

.news-list {
    position: relative;
    top: 1.6rem;
    padding: 0rem .3rem;
    overflow-y: scroll;
    font-size: .34rem;
}

.news-section {
    padding: .32rem 0;
    border-bottom: 1px solid rgba(221, 221, 221, 0.6);
}
.news-title {
    font-size: .34rem;
    color: #333;
    font-weight: normal;
}
.news-info {
    margin-top: .12rem;
    color: #999;
    font-size: .18rem;
}
.news-label {
    color: #f85959;
    text-align: center;
    border: .01rem solid rgba(248, 89, 89, 0.5);
    border-radius: .04rem;
    padding: 0rem .04rem;
    margin-right: .1rem;
}
.news-label-gg {
    color: #2a90d7;
    text-align: center;
    border: .01rem solid rgba(42, 144, 215, 0.5);
    border-radius: .04rem;
    margin-right: .1rem;
    padding: 0rem .04rem;
}
.news-common {
    margin-right: .1rem;
    color: #999;
}
.news-info-left {
    width: 4.26rem;
    position: relative;
    display: inline-block;
}
.news-img-right {
    position: relative;
    display: inline-block;
}
.news-img {
    width: 2.5rem;
    height: 1.47rem;
}
.news-img-list {
    font-size: 0;
    list-style: none;
}
.list-img-holder {
    display: inline-block;
    padding-right: .04rem;
}
.list-img-holder img {
    width: 2.25rem;
    height: 1.47rem;
}
.news-large-img {
    margin-top: .12rem;
    overflow: hidden;
    width: 100%;
    position: relative;
    height: 3.75rem;
}
.news-large-img img {
    width: 6.9rem;
    height: 3.88rem;
}
.loading-more {
    text-align: center;
    color: #999;
    font-size: .26rem;
    padding: .4rem;
}

.channels{
    position: absolute;
    width: 100%;
    height: 100%;
    background: #f8f8f8;
}
.toolbar {
    height: 1rem;
    position: relative;
    width: 100%;
    color: #fff;
    font-size: .31rem;
    line-height: 1rem;
    background-color: #d43d3d;
    text-align: center;
}
.backbtn {
    float: left;
    margin-top: .18rem;
    margin-left: .2rem;
    width: .46rem;
    height: .6rem;
    background: url(data:image/png;base64,) no-repeat 50%;
    background-size: 100%;
    /* display: inline-block; */
}
.toolbar-title {
    margin-left: -.2rem;
}
.remove-channels {
    font-size: .2rem;
}
.remove-title {
    color: #8e8e8e;
    padding: .1rem .2rem;
    font-family: Arial, Helvetica, Hiragino Sans GB, Microsoft YaHei, sans-serif
}
.mark {
    color: red !important;
}
.remove-channels-list {
    padding: .16rem 0rem;
    /* margin: .1rem 0rem; */
    list-style: none;
    font-size: .32rem;
    color: #333;
    font-weight: 400;
    font-family: Arial, Helvetica, Hiragino Sans GB, Microsoft YaHei, sans-serif
}
.channels-list-item {
    display: inline-block;
    /* margin-bottom: .2rem; */
    border: 1px solid #ccc;
    padding: .08rem .43rem;
    margin: 0rem .16rem .2rem .16rem;
    animation: zoomIn .3s ease;
    -webkit-animation: zoomIn .3s ease;
}
.current-channels {
    background-color: #f0f0f0;
}
@-webkit-keyframes zoomIn {
0% {
        opacity: 0;
        -webkit-transform: scale3d(.3, .3, .3);
        transform: scale3d(.3, .3, .3);
}
50% {
        opacity: 1;
}
}
@keyframes zoomIn {
0% {
        opacity: 0;
        -webkit-transform: scale3d(.3, .3, .3);
        transform: scale3d(.3, .3, .3);
}
50% {
        opacity: 1;
}
}

.search-list {
    position: relative;
    padding: 0rem .3rem;
    overflow-y: scroll;
    font-size: .34rem;
}
.search-page {
    position: absolute;
    width: 100%;
    height: 100%;
    background: #fff;
}
.toolbar {
    height: 1rem;
    position: relative;
    width: 100%;
    color: #fff;
    font-size: .31rem;
    line-height: 1rem;
    background-color: #d43d3d;
    text-align: center;
}
.backbtn {
    float: left;
    margin-top: .18rem;
    margin-left: .2rem;
    width: .46rem;
    height: .6rem;
    background: url(data:image/png;base64,) no-repeat 50%;
    background-size: 100%;
    /* display: inline-block; */
}
.search-title {
    font-size: .44rem;
    margin-left: -.4rem;
    font-weight: 700;
}
.search-content {
    padding: .1rem;
    font-size: .31rem;
    background: #c9c9c9;
}
.search-input {
    padding: .1rem .2rem .1rem .8rem;
    width: 7.3rem;
    border-radius: .1rem;
    outline: 0;
    border: 0;
    font-size: .31rem;
    -webkit-appearance: none;
}
.search-chooser {
    display: inline-block;
    width: .8rem;
    height: .54rem;
    position: absolute;
    background: url(http://s3.pstatp.com/image/toutiao_mobile/search_icons.png?ver=201408311655) #fff no-repeat 5px 2px;
    background-size: .4rem;
    background-position: 5px -35px;
}
.search-tip {
    text-align: center;
    font-size: .3rem;
}
.search-tip li {
    display: inline-block;
    background: #f5f5f5;
    padding: .1rem;
    color: #363636;
    margin: .3rem .1rem 0;
}
*{
    margin: 0;
    padding: 0;
}